.wrapper {
  max-width: 1310px;
  min-width: 350px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  margin: 50px auto;
  background-color: rgb(53, 53, 53);
  font-family: sans-serif;
  color: white;
}

.button {
  padding: 20px 50px;
  margin-top: 50px;
  border: none;
  background-color: aqua;
  border-radius: 5px;
  font-weight: 800;
  font-size: larger;
  color: #fff;
}

.text {
  margin: 20px 0;
  color: white;
  font-size: 20px;
}